/* OrderHistory.module.css */

.container {
  margin: 0;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.title {
  font-size: 20px;
  font-weight: 700;
  color: #1f2937;
}

.orderCount {
  font-size: 14px;
  color: #6b7280;
}

.ordersList {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.orderCard {
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #e5e7eb;
  padding: 24px;
}

.orderHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
}

.restaurantInfo h3 {
  font-weight: 600;
  color: #1f2937;
  margin: 0;
}

.restaurantInfo p {
  font-size: 0.875rem;
  color: #6b7280;
  margin: 0;
}

.statusBadge {
  padding: 4px 12px;
  border-radius: 9999px;
  font-size: 12px;
  font-weight: 500;
}

.statusCompleted {
  background-color: #dcfce7;
  color: #166534;
}

.statusProcessing {
  background-color: #dbeafe;
  color: #1e40af;
}

.statusPending {
  background-color: #fef3c7;
  color: #92400e;
}

.orderItems {
  margin-bottom: 16px;
}

.orderItems p {
  font-size: 14px;
  color: #6b7280;
  margin-bottom: 8px;
}

.orderMeta {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
  margin-bottom: 16px;
}

@media (min-width: 768px) {
  .orderMeta {
    grid-template-columns: repeat(3, 1fr);
  }
}

.metaItem {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  color: #6b7280;
}

.metaIcon {
  height: 16px;
  width: 16px;
}

.actionButtons {
  display: flex;
  gap: 12px;
}

.reorderButton {
  flex: 1;
  background-color: #2563eb;
  color: white;
  padding: 8px;
  border-radius: 8px;
  font-weight: 500;
  transition: background-color 0.2s;
  border: none;
  cursor: pointer;
}

.reorderButton:hover {
  background-color: #1d4ed8;
}

.detailButton {
  padding: 8px 16px;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  background-color: white;
  color: #374151;
  transition: background-color 0.2s;
  cursor: pointer;
}

.detailButton:hover {
  background-color: #f9fafb;
}